.dialog a {
  text-decoration: none;
  color: black;
}

.dialog {
  padding: 1px;

}

.dialog:hover {
  background-color: white;
  padding: 0;
  border-radius: 10px;
  /*цвет синхронить*/
  border: solid 1px black;
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);


}

.dialog a:hover {
  text-decoration: underline;
  color: black;
}


.linkDialog {
  height: 50px;
  display: flex;
  align-items: center;
}

.avatar {
  width: 40px;
  height: 40px;
  margin: 0 10px;
  background: white;
  border-radius: 40%;
}

.dialogsItems .active {
  color: aliceblue;

}
